#head{
        background-color: $primary600;
        padding: 17px 20px;
        position: relative;
        z-index: 1;
        height: 66px;
        box-sizing:border-box;
        display:flex;
        .account_block{
                margin-left:auto;
                display:flex;
                margin-right:10px;
                cursor:pointer;
                position: relative;
                .avatar{
                        width:33px;
                        height:33px;
                        border-radius:30px;
                        background:#eee;
                        float:left;
                }
                .name_account{
                        margin-left:10px;
                        color:#fff;
                }
                span{
                        display:block;
                        white-space: nowrap;
                        line-height:18px;
                        font-size:14px;
                        color:#fff;
                        &.account{
                                font-size:12px;
                                opacity:0.4;
                                line-height:12px;
                        }
                }
        }
        .storeselector,
        .storeselect{
                display:flex;
                line-height:33px;
                .storename{
                        line-height:34px;
                        color:#fff;
                        opacity:0.85;
                        padding:0 11px 0 22px;
                        font-size:16px;
                }
                i{
                        line-height:40px;
                        font-size:18px;
                        color:#fff;
                        opacity:0.5;
                }
                img{margin-top:0px;}
        }
}
.dropsubmenu,
.dropsubmenustores{
        display:none;
        opacity:0;
        position:absolute;
        transition: opacity 200ms ease;
        z-index:1000;
        background:$primary800;
        top:66px;
        right:0px;
        min-width:180px;
        height:auto;
        border-bottom-left-radius: 8px;
        -webkit-border-bottom-left-radius: 8px;
        -moz-border-radius-bottomleft: 8px;
        &.active{
                display:inherit;
                opacity:1;
        }
        > div{
                list-style:none;
                display:block;
                width:100%;
                border-radius:4px;
                a{
                        font-size:13px;
                        display:block;
                        padding:11px;
                        padding-left:16px;
                        transition: all 200ms;
                        color:#fff;
                        border-left:5px solid $primary800;

                        i{ margin-right:11px; color:#fff; }

                        &.logout{
                                border-top:$border-color;
                        }

                        &:hover{
                                background:$primary900;
                                border-left:5px solid $primary500;
                        }
                }

        }
}
.storeselect{
        cursor:pointer;
}

.dropsubmenustores{
        left:-500px;
        right:inherit;
        display: flex;
        flex-direction: column;
        width:auto;
        border-bottom-right-radius: 8px;
        -webkit-border-bottom-right-radius: 8px;
        -moz-border-radius-bottomright: 8px;
        &.active{
                left:0px;

        }
        a {
                font-size:13px;
                display:block;
                padding:11px;
                padding-left:66px;
                padding-right:33px;
                transition: all 200ms;
                color:#fff;
                border-left:5px solid $primary800;
                &:hover{
                        background:$primary900;
                        border-left:5px solid $primary500;
                }
        }
}